.about_container {
    display: grid;
    grid-auto-rows: 1fr;
    gap: 2.5rem;
    justify-content: center;
}

.about_me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    display: grid;
    place-items: center;
}

.about_me-image {
    border-radius: 2rem 2rem 2rem 2rem;
    overflow: hidden;
    transition: var(--transition);
}

.about_me-image:hover {
    transform:scale(1.1)
}

.about_cards {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 1.5rem;
}

.about_card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
}

.about_card:hover {
    background-color: rgb(79, 79, 79);
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about_icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about_card h5 {
    font-size: 1.5rem;
}

.about_card p {
    font-size: 0.95rem;
}

.about_card small {
    font-size: 0.8rem;
    color: var(--color-light);
}

.about_content {
    display: grid;
    align-items: center;
}

.about_content p {
    margin: 2rem;
    color: var(--color-light); 
}

.contact_button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 5rem;
}

.bio_header {
    text-align: center;
}

/* Media Queries - Medium */
@media screen and (max-width: 1024px) {
    .about_container {
        grid-template-columns: 1fr;
        gap: 0;
    }
    .about_cards {
        grid-template-columns: repeat(1, 1fr);
    }
    .about_me {
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about_content p {
        margin: 1rem 0 1.5;
    }
}

/* Media Queries - Small */
@media screen and (max-width: 600px) {
    .about_me {
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about_card {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }
    .about_cards {
        grid-template-columns: repeat(1, 1fr);
    }
    .about_content {
        /* grid-template-columns: 1fr 1fr; */
        /* width: 70%; */
        text-align: center;
    }
    .about_content p {
        /* width: 70%; */
        /* margin: 0 auto 3rem; */
        margin: 1.5rem 0;
    }
}