#portfolio {
    margin-top: 0px;
}

.portfolio_container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: 1fr;
    gap: 2.5rem;
    justify-content: center;
}

.portfolio_item {
    background: var(--color-bg-variant);
    padding: 1.3rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

#portfolio_btn{
    flex-direction: column;
    justify-content: flex-end;
}

.portfolio_item:hover {
    border-color: var(--color-primary-variant);
    background-color: rgb(79, 79, 79);
}

.portfolio_item h3 {
    margin: 1.2 0 2rem;
}

.portfolio_item img {
    height: 250px;
    width: 250px;
    margin: 2rem;
    border-radius: 2rem;
}

.portfolio_item p {
    margin-bottom: 2.25rem;
}

.portfolio_links {
    bottom: 0;
    position: absolute;
    bottom: 0;
}

/* Media Queries - Medium */
@media screen and (max-width: 1024px) {
    .portfolio_container {
        grid-template-columns: 1fr, 1fr;
        gap: 1.2rem;
    }
}

/* Media Queries - Small */
@media screen and (max-width: 600px) {
    .portfolio_container {
        grid-template-columns: 1fr;
        gap: 1rem;
    }
}